<template>
  <div class="in-coder-panel">
    <textarea
      style="height: 100% !important"
      ref="textarea"
      v-model="code"
    ></textarea>
  </div>
</template>

<script>
// 
import _CodeMirror from "codemirror/lib/codemirror";

//
import "codemirror/addon/hint/sql-hint";
// 
import "codemirror/lib/codemirror.css";
//  options 
// , 
import "codemirror/theme/darcula.css";
// 
import "codemirror/addon/hint/show-hint.css";

import {
  reactive,
  defineComponent,
  toRefs,
  getCurrentInstance,
  onMounted,
  watch,
  ref,
  onBeforeUnmount,
} from "vue";
import { format } from "sql-formatter";

// codemirror  /addon/mode/loadmode.js  /mode/meta.js 
//  vue  JS  JS 
import "codemirror/mode/clike/clike.js";
import "codemirror/mode/r/r.js";

const codemirrorThemList = [];
const requireModules = require.context("codemirror/theme/", false, /\.css$/);
requireModules.keys().forEach((value) => {
  const newValue = value.replace(/^\.\//, "").replace(/\.css$/, "");
  codemirrorThemList.push(newValue);
});

// 
const CodeMirror = window.CodeMirror || _CodeMirror;
let coder = null; // 

export default defineComponent({
  name: "codeEditor",
  props: {
    value: {
      type: String,
      default: "",
    },
    heightSize: {
      type: Number,
      default: 500,
    },
    scene: {
      type: String,
      default: "look", // add:  edit:  look: 
    },
    eventType: {
      type: String,
      default: "blur", // 'change', 'blur'codemirror
    },
    theme: {
      type: String,
      default: "darcula", // 
    },
  },
  setup(props, { emit }) {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      code: props.value, // 
      // 
      options: {
        // mode: "text/x-java", // 
        mode: "text/x-mariadb",
        // 
        tabSize: 2,
        //  JS 
        theme: props.theme,
        // 
        lineNumbers: true,
        readOnly:
          props.scene === "add" || props.scene === "edit" ? false : "nocursor", // true:   false:  'nocursor' ,
      },
      // 
      initialize: () => {
        // 
        coder = CodeMirror.fromTextArea(proxy.$refs.textarea, data.options);
        const h = props.heightSize + "px";
        coder.setSize("auto", h);
        // 'change''blur'
        coder.on(props.eventType, (coder) => {
          if (coder.getValue() != format(coder.getValue())) {
            coder.setValue(format(coder.getValue()));
          }
          emit("update:value", coder.getValue());
        });
      },
      // 
      importThemDynamic: () => {
        return new Promise((resolve) => {
          codemirrorThemList.forEach((value) => {
            if (props.theme === value) {
              import(`codemirror/theme/${props.theme}.css`);
              resolve();
            }
          });
        });
      },
    });
    onMounted(() => {
      // console.log('value:', props.value)
      data.importThemDynamic().then(() => {
        data.initialize();
      });
    });
    onBeforeUnmount(() => {
      coder.off(props.eventType);
    });
    return {
      ...toRefs(data),
    };
  },
});
</script>

<style>
.in-coder-panel {
  flex-grow: 1;
  display: flex;
  position: relative;
}

.in-coder-panel .CodeMirror {
  flex-grow: 1;
  text-align: left !important;
  z-index: 1;
}

.in-coder-panel .CodeMirror .CodeMirror-code {
  line-height: 20px;
}
</style>
